.container1{
    margin: 10px 150px;
  
}


.landingContainer .container1 nav{
  
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 25px;
    background-color: rgb(9, 180, 177);

}

.container1 nav .logo{
    padding-left: 500px;
}

.logo{
    font-weight: 700;
    font-family: cursive;
}

.loginRegister{
    display: flex;
    align-items: center;
    gap: 10px;
}

.loginRegister span{
    background: #006dfe;
    padding: 5px 10px;
    color: white;
    border-radius: 5px;
    font-size: 15px;
    cursor: pointer;
}

.loginRegister span:hover{
    background: #dfbb18;
   
}

.container1 .section1{
    
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    background-color: rgb(9, 180, 177);
    padding: 10px 20px;
}

.s1-div1 h1{
    font-weight: bolder;
}

.s1-div1 i{
    color: red;
}

.s1-image img{
    width: 300px;
}

#doctor1{
    width: 390px;
}

.s1-div1 button{
    padding: 5px;
    background-color: #006dfe;
    border-radius: 5px;
    color: white;
    border: 1px solid black;
}

.section2{
    background-color: rgba(151, 155, 156, 0.622);
    display: flex;
    justify-content: space-evenly;
    padding: 20px;
    margin-top: 20px;
}

.s2-div{
    display: flex;
    gap: 10px;
    font-weight: bolder;
}

.s2-div .number1{
    font-size: larger;
    color: #00aadc;
    font-weight: 700;
}

.s2-div .number2{
    font-size: larger;
    color: #dfa907;
    font-weight: 700;
}

.s2-div .number3{
    font-size: larger;
    color: #fe765b;
    font-weight: 700;
}

.s2-div i{
    font-size: 30px;
    color: white;
}

.s2-div-div{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
}

.s2-div-div.stethoscope{
    background-color: #00aadc;
}

.s2-div-div.house{
    background-color: #dfa907;
}

.s2-div-div.lines{
    background-color: #fe765b;
}

.s3-img.doctor1{
    -webkit-transform: scaleX(-1);
     transform: scaleX(-1);
}

.section3{
    margin-top: 20px;
}

.s3-div{
    display: flex;
    align-items: center;
    gap: 20px;

}

.s3-div-fdExp{
    display: flex;
    gap: 20px  ;
}

.s3-div-fdExp-div{
    display: flex;
    flex-direction: column;
    
}

.s3-div-fdExp-div div{
    height: 10px;
    width: 100px;
    border-radius: 5px;
}


.sideImage{
    width: 400px;
    height: 500px;
}

.sideImage img{
    width: inherit;
}

.s3-div.right{
    flex-direction: row-reverse;


}

.s3-div-div button{
    background-color: #006dfe;
    padding: 10px 15px;
    color: white;
    border-radius: 10px;
    border: none;
}

.section4{
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}

.s4-div1{
    width: 60%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
}

.s4-div1-icon{
    width: 50px;
    height: 50px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    margin-bottom: 15px;
}

.s4-div1-icon i{
    font-size: 30px;
}

.s4-div2{
    width: 40%;
    padding-left: 50px;
    font-weight: 500;
}

.section5{
    margin-top: 200px;
    display: flex;
    justify-content: center;
    gap: 100px;
    height: 395px;
    align-items: center;
    background-color: #006dfe;
    color: white;
}

/* .s5-div1{
    width: 400px;
    height: 500px;
} */
.s5-div1 img{
    position: relative;
    top: -70px;
    width: 480px ;
}

.s5-div2 button{
    background-color: white;
    color: black;

}


.section7{
    margin-top: 50px;
    padding: 60px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #006dfe;
    color: white;
    z-index: 1;
}

.section7 button{
    background-color: white;
    color: black;
    padding: 10px 20px;
    border-radius: 10px;
    border: none;
}

.section6  {
    margin-top: 100px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}


.s6-div1{
    
    width: 30%;
    position: relative;
}

.s6-div2{
    width: 50%;
}

.s6-div2 h2{
    font-weight: 700;
}

.s6-div2 img{
    width: 200px;
    padding: 5px 10px;
}


.s6-mobileapp{
    width: 330px;
    height: 600px;
    border: 10px solid black;
    border-radius: 10px;
    background-color: #d9e6ea;
    
    
    
    
}

.s6-time{
    position: relative;
    left:10px;
    font-size: 14px;
    font-weight: 500;
}

.s6-mobileFront{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    text-align: center;

}

.s6-mobileapp button{
    padding: 5px 15px;
    background-color: #f4b821;
    border: none;
    border-radius: 40px;
    font-weight: 500;
}

.s6-mobileapp img{
    width: 200px;
    
}

.s6-rating{
    position: absolute;
    top:-20px;
    right: -90px; 
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 250px;
    background-color: rgb(244, 227, 227);
    padding: 5px;
    border-radius: 25px;
}

.s6-imageRating{
    width: 55px;
    height: 55px;
    border-radius: 50%;
    background-color: rgb(65, 230, 147);
    border:5px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

}

.s6-imageRating img{
    width: 30px;
}

.s6-star i{
    color: orange;
}

.s6-star{
    font-weight: 500;
}

.footer{   
    background-color: #151d34;
    color: white;
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    padding: 50px 0px;
    

}

.footer > div{
    display: flex;
    flex-direction: column;
    gap: 10px;
    /* justify-content: space-between; */
   

}


.flinks{
    display: flex;
    gap: 10px;
}

.socialLink i{
    margin-right: 10px;
}

#vectorImage2{
    width: 240px;
}

#vectorImage3{
    width: 280px;
}